"use client";

import Container from "./components/container";
import jobsApi from "./lib/jobs-api";
import Filter from "./components/filter";
import axios from "axios";
import { useEffect, useState } from "react";

type Company = {
  company: string;
  url: string;
  details: string;
  region: string[];
};

const getJobs = async () => {
  const { data } = await axios.get(jobsApi());
  const array: Array<string> = data
    .split("------------ | ------- | -------\n")[1]
    .split("\n");
  const jobs = array.map((item) => {
    const [namePart, url, regions] = item.split("|");
    const companyMatch = namePart.match(/\[(.*?)\]\((.*?)\)/);
    return companyMatch && url && regions
      ? {
          company: companyMatch ? companyMatch[1] : "",
          url: url.trim(),
          details: companyMatch ? companyMatch[2].trim() : "",
          region: regions.split(",").map((region) => region.trim()),
        }
      : "";
  });

  const filteredJobs = jobs.filter((job) => job !== "");

  return filteredJobs;
};

export default function Home() {
  const [jobs, setJobs] = useState<Array<Company>>([]);

  useEffect(() => {
    (async () => {
      setJobs(await getJobs());
    })();
  }, []);

  console.log(jobs);

  return (
    <div className="">
      <Container>
        <Filter />

        <div className="overflow-x-auto pt-10">
          <table className="min-w-full table-auto">
            <thead>
              <tr className="bg-gray-100">
                <th className="px-4 py-2 text-left text-sm font-medium text-gray-600">
                  Company
                </th>
                <th className="px-4 py-2 text-left text-sm font-medium text-gray-600">
                  Website
                </th>
                <th className="px-4 py-2 text-left text-sm font-medium text-gray-600">
                  Region
                </th>
                <th className="px-4 py-2 text-left text-sm font-medium text-gray-600">
                  Detail
                </th>
              </tr>
            </thead>
            <tbody>
              {jobs.map((job, idx) => (
                <tr key={idx} className="border-t border-b border-gray-200">
                  <td className="px-4 py-2 text-sm text-gray-800">
                    {job.company}
                  </td>
                  <td className="px-4 py-2 text-sm text-gray-800">
                    <a href={job.url} className="text-blue-600 hover:underline">
                      {job.url}
                    </a>
                  </td>
                  <td className="px-4 py-2 text-sm text-gray-800">Worldwide</td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>
      </Container>
    </div>
  );
}
